.theme-cardtable .Button {
  position: relative;
  display: inline-block;
  line-height: 20px;
  padding: 0 6px;
  margin-right: 2px;
  white-space: nowrap;
  outline: 0;
  border-radius: 0;
  margin-bottom: 2px;
  user-select: none;
  -ms-user-select: none;
}
.theme-cardtable .Button:last-child {
  margin-right: 0;
}
.theme-cardtable .Button .fa, .theme-cardtable .Button .fas, .theme-cardtable .Button .far {
  margin-left: -3px;
  margin-right: -3px;
  min-width: 16px;
  text-align: center;
}
.theme-cardtable .Button--hasContent .fa, .theme-cardtable .Button--hasContent .fas, .theme-cardtable .Button--hasContent .far {
  margin-right: 3px;
}
.theme-cardtable .Button--ellipsis {
  overflow: hidden;
  text-overflow: ellipsis;
}
.theme-cardtable .Button--fluid {
  display: block;
  margin-left: 0;
  margin-right: 0;
}
.theme-cardtable .Button--color--default {
  transition: color 50ms, background-color 50ms;
  background-color: #117039;
  color: white;
}
.theme-cardtable .Button--color--default:hover {
  transition: color 0ms, background-color 0ms;
}
.theme-cardtable .Button--color--default:focus {
  transition: color 100ms, background-color 100ms;
}
.theme-cardtable .Button--color--default:hover, .theme-cardtable .Button--color--default:focus {
  background-color: #1c8247;
  color: white;
}
.theme-cardtable .Button--color--caution {
  transition: color 50ms, background-color 50ms;
  background-color: #be6209;
  color: white;
}
.theme-cardtable .Button--color--caution:hover {
  transition: color 0ms, background-color 0ms;
}
.theme-cardtable .Button--color--caution:focus {
  transition: color 100ms, background-color 100ms;
}
.theme-cardtable .Button--color--caution:hover, .theme-cardtable .Button--color--caution:focus {
  background-color: #d67313;
  color: white;
}
.theme-cardtable .Button--color--danger {
  transition: color 50ms, background-color 50ms;
  background-color: #9a9d00;
  color: white;
}
.theme-cardtable .Button--color--danger:hover {
  transition: color 0ms, background-color 0ms;
}
.theme-cardtable .Button--color--danger:focus {
  transition: color 100ms, background-color 100ms;
}
.theme-cardtable .Button--color--danger:hover, .theme-cardtable .Button--color--danger:focus {
  background-color: #afb30a;
  color: white;
}
.theme-cardtable .Button--color--transparent {
  transition: color 50ms, background-color 50ms;
  background-color: #117039;
  color: white;
  background-color: rgba(17, 112, 57, 0);
  color: rgba(255, 255, 255, 0.5);
}
.theme-cardtable .Button--color--transparent:hover {
  transition: color 0ms, background-color 0ms;
}
.theme-cardtable .Button--color--transparent:focus {
  transition: color 100ms, background-color 100ms;
}
.theme-cardtable .Button--color--transparent:hover, .theme-cardtable .Button--color--transparent:focus {
  background-color: #1c8247;
  color: white;
}
.theme-cardtable .Button--disabled {
  background-color: #363636 !important;
}
.theme-cardtable .Button--selected {
  transition: color 50ms, background-color 50ms;
  background-color: #9d0808;
  color: white;
}
.theme-cardtable .Button--selected:hover {
  transition: color 0ms, background-color 0ms;
}
.theme-cardtable .Button--selected:focus {
  transition: color 100ms, background-color 100ms;
}
.theme-cardtable .Button--selected:hover, .theme-cardtable .Button--selected:focus {
  background-color: #b31212;
  color: white;
}
.theme-cardtable .Button--altSelected--bottom::after {
  content: "";
  position: absolute;
  bottom: 0;
  right: 0;
  left: 0;
  height: 2px;
  width: 100%;
  background-color: #ffffff;
  border-radius: 0;
}
.theme-cardtable .Button--altSelected--right::after {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  height: 100%;
  width: 3px;
  background-color: #ffffff;
  border-radius: 0;
}
.theme-cardtable .Layout {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  color: #ffffff;
  background-color: #117039;
  background-image: linear-gradient(to bottom, #117039 0%, #117039 100%);
}
.theme-cardtable .Layout__titleBar {
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  width: 100%;
  height: 32px;
}
.theme-cardtable .Layout__content {
  position: fixed;
  top: 32px;
  bottom: 0;
  left: 0;
  right: 0;
  overflow-x: hidden;
  overflow-y: hidden;
  margin-bottom: -6px;
  scrollbar-base-color: #0d542b;
  scrollbar-face-color: #16914a;
  scrollbar-3dlight-color: #117039;
  scrollbar-highlight-color: #117039;
  scrollbar-track-color: #0d542b;
  scrollbar-arrow-color: #5ae695;
  scrollbar-shadow-color: #16914a;
}
.theme-cardtable .Layout__content--scrollable {
  overflow-y: scroll;
  margin-bottom: 0;
}
.theme-cardtable .Layout__dimmer {
  position: fixed;
  top: 32px;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(39, 148, 85, 0.25);
  pointer-events: none;
}
.theme-cardtable .Layout__toast {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  font-size: 12px;
  height: 40px;
  line-height: 39px;
  padding: 0 12px;
  background-color: #09381d;
  color: rgba(255, 255, 255, 0.8);
}
.theme-cardtable .Layout__resizeHandle__se {
  position: fixed;
  bottom: 0;
  right: 0;
  width: 20px;
  height: 20px;
  cursor: se-resize;
}
.theme-cardtable .Layout__resizeHandle__s {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  height: 6px;
  cursor: s-resize;
}
.theme-cardtable .Layout__resizeHandle__e {
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  width: 3px;
  cursor: e-resize;
}
.theme-cardtable .Input {
  position: relative;
  display: inline-block;
  width: 120px;
  border: 1px solid #88bfff;
  border: 1px solid rgba(136, 191, 255, 0.75);
  border-radius: 0px;
  color: #fff;
  background-color: #000;
  background-color: rgba(0, 0, 0, 0.75);
  padding: 0 4px;
  margin-right: 2px;
  line-height: 17px;
  overflow: visible;
}
.theme-cardtable .Input--fluid {
  display: block;
  width: auto;
}
.theme-cardtable .Input__baseline {
  display: inline-block;
  color: transparent;
}
.theme-cardtable .Input__input {
  display: block;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  border: 0;
  outline: 0;
  width: 100%;
  font-size: 12px;
  line-height: 17px;
  height: 17px;
  margin: 0;
  padding: 0 6px;
  font-family: Verdana, sans-serif;
  background-color: transparent;
  color: #fff;
  color: inherit;
}
.theme-cardtable .Input__input:-ms-input-placeholder {
  font-style: italic;
  color: #777;
  color: rgba(255, 255, 255, 0.45);
}
.theme-cardtable .NumberInput {
  position: relative;
  display: inline-block;
  border: 1px solid #FFFFFF;
  border: 1px solid rgba(255, 255, 255, 0.75);
  border-radius: 0px;
  color: #FFFFFF;
  background-color: #000;
  background-color: rgba(0, 0, 0, 0.75);
  padding: 0 4px;
  margin-right: 2px;
  line-height: 17px;
  text-align: right;
  overflow: visible;
  cursor: n-resize;
}
.theme-cardtable .NumberInput--fluid {
  display: block;
}
.theme-cardtable .NumberInput__content {
  margin-left: 6px;
}
.theme-cardtable .NumberInput__barContainer {
  position: absolute;
  top: 2px;
  bottom: 2px;
  left: 2px;
}
.theme-cardtable .NumberInput__bar {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 3px;
  box-sizing: border-box;
  border-bottom: 1px solid #FFFFFF;
  background-color: #FFFFFF;
}
.theme-cardtable .NumberInput__input {
  display: block;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  border: 0;
  outline: 0;
  width: 100%;
  font-size: 12px;
  line-height: 17px;
  height: 17px;
  margin: 0;
  padding: 0 6px;
  font-family: Verdana, sans-serif;
  background-color: #000;
  color: #fff;
  text-align: right;
}
.theme-cardtable .ProgressBar {
  display: inline-block;
  position: relative;
  width: 100%;
  padding: 0 6px;
  border-radius: 0;
  background-color: rgba(0, 0, 0, 0.5);
  transition: border-color 500ms;
}
.theme-cardtable .ProgressBar__fill {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  transition: background-color 500ms, width 500ms;
}
.theme-cardtable .ProgressBar__content {
  position: relative;
  line-height: 17px;
  width: 100%;
  text-align: right;
}
.theme-cardtable .ProgressBar--color--default {
  border: 1px solid black;
}
.theme-cardtable .ProgressBar--color--default .ProgressBar__fill {
  background-color: black;
}
.theme-cardtable .Section {
  position: relative;
  margin-bottom: 6px;
  background-color: #1a1a1a;
  background-color: rgba(0, 0, 0, 0.33);
  box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.5);
  box-sizing: border-box;
}
.theme-cardtable .Section:last-child {
  margin-bottom: 0;
}
.theme-cardtable .Section__title {
  position: relative;
  padding: 6px;
  border-bottom: 2px solid #000000;
}
.theme-cardtable .Section__titleText {
  font-size: 14px;
  font-weight: bold;
}
.theme-cardtable .Section__buttons {
  position: absolute;
  display: inline-block;
  right: 6px;
  margin-top: -1px;
}
.theme-cardtable .Section__content {
  padding: 8px 6px;
}
.theme-cardtable .Section--level--1 .Section__titleText {
  font-size: 14px;
}
.theme-cardtable .Section--level--2 .Section__titleText {
  font-size: 13px;
}
.theme-cardtable .Section--level--3 .Section__titleText {
  font-size: 12px;
}
.theme-cardtable .Section--level--2,
.theme-cardtable .Section--level--3 {
  background-color: transparent;
  box-shadow: none;
  margin-left: -6px;
  margin-right: -6px;
}
.theme-cardtable .TitleBar {
  background-color: #381608;
  border-bottom: 1px solid #161616;
  box-shadow: 0 2px 2px rgba(0, 0, 0, 0.1);
  user-select: none;
  -ms-user-select: none;
}
.theme-cardtable .TitleBar__clickable {
  color: rgba(255, 255, 255, 0.5);
  background-color: #381608;
  transition: color 250ms, background-color 250ms;
}
.theme-cardtable .TitleBar__clickable:hover {
  color: white;
  background-color: #cc0000;
  transition: color 0ms, background-color 0ms;
}
.theme-cardtable .TitleBar__title {
  position: absolute;
  top: 0;
  left: 46px;
  color: rgba(255, 255, 255, 0.75);
  font-size: 14px;
  line-height: 31px;
  white-space: nowrap;
}
.theme-cardtable .TitleBar__dragZone {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 32px;
}
.theme-cardtable .TitleBar__statusIcon {
  position: absolute;
  top: 0;
  left: 12px;
  transition: color 0.5s;
  font-size: 20px;
  line-height: 32px !important;
}
.theme-cardtable .TitleBar__minimize {
  position: absolute;
  top: 6px;
  right: 46px;
}
.theme-cardtable .TitleBar__close {
  position: absolute;
  top: -1px;
  right: 0;
  width: 45px;
  height: 32px;
  font-size: 20px;
  line-height: 31px;
  text-align: center;
}
.theme-cardtable .Button {
  border-color: #FFFFFF;
  border-width: 2px;
  border-style: solid;
}