.Tooltip {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  font-style: normal;
  font-weight: normal;
}

.Tooltip::after {
  position: absolute;
  display: block;
  white-space: nowrap;
  z-index: 2;
  padding: 6px 10px;
  transform: translateX(-50%);
  pointer-events: none;
  visibility: hidden;
  opacity: 0;
  text-align: left;
  content: attr(data-tooltip);
  transition: all 150ms;
  background-color: #000000;
  box-shadow: 1px 1px 15px -1px rgba(0, 0, 0, 0.5);
  border-radius: 2px;
}

.Tooltip:hover::after {
  transition: all 70ms;
  pointer-events: none;
  visibility: visible;
  opacity: 1;
}

.Tooltip--long::after {
  width: 250px;
  white-space: normal;
}

.Tooltip--top::after {
  bottom: 100%;
  left: 50%;
  transform: translateX(-50%) translateY(8px);
}

.Tooltip--top:hover::after {
  transform: translateX(-50%) translateY(-8px);
}

.Tooltip--bottom::after {
  top: 100%;
  left: 50%;
  transform: translateX(-50%) translateY(-8px);
}

.Tooltip--bottom:hover::after {
  transform: translateX(-50%) translateY(8px);
}

.Tooltip--bottom-left::after {
  top: 100%;
  right: 50%;
  transform: translateX(12px) translateY(-8px);
}

.Tooltip--bottom-left:hover::after {
  transform: translateX(12px) translateY(8px);
}

.Tooltip--bottom-right::after {
  top: 100%;
  left: 50%;
  transform: translateX(-12px) translateY(-8px);
}

.Tooltip--bottom-right:hover::after {
  transform: translateX(-12px) translateY(8px);
}

.Tooltip--left::after {
  top: 50%;
  right: 100%;
  transform: translateX(8px) translateY(-50%);
}

.Tooltip--left:hover::after {
  transform: translateX(-8px) translateY(-50%);
}

.Tooltip--right::after {
  top: 50%;
  left: 100%;
  transform: translateX(-8px) translateY(-50%);
}

.Tooltip--right:hover::after {
  transform: translateX(8px) translateY(-50%);
}

