.theme-ntos .Button {
  position: relative;
  display: inline-block;
  line-height: 20px;
  padding: 0 6px;
  margin-right: 2px;
  white-space: nowrap;
  outline: 0;
  border-radius: 2px;
  margin-bottom: 2px;
  user-select: none;
  -ms-user-select: none;
}
.theme-ntos .Button:last-child {
  margin-right: 0;
}
.theme-ntos .Button .fa, .theme-ntos .Button .fas, .theme-ntos .Button .far {
  margin-left: -3px;
  margin-right: -3px;
  min-width: 16px;
  text-align: center;
}
.theme-ntos .Button--hasContent .fa, .theme-ntos .Button--hasContent .fas, .theme-ntos .Button--hasContent .far {
  margin-right: 3px;
}
.theme-ntos .Button--ellipsis {
  overflow: hidden;
  text-overflow: ellipsis;
}
.theme-ntos .Button--fluid {
  display: block;
  margin-left: 0;
  margin-right: 0;
}
.theme-ntos .Button--color--default {
  transition: color 50ms, background-color 50ms;
  background-color: #384e68;
  color: white;
}
.theme-ntos .Button--color--default:hover {
  transition: color 0ms, background-color 0ms;
}
.theme-ntos .Button--color--default:focus {
  transition: color 100ms, background-color 100ms;
}
.theme-ntos .Button--color--default:hover, .theme-ntos .Button--color--default:focus {
  background-color: #465e7a;
  color: white;
}
.theme-ntos .Button--color--caution {
  transition: color 50ms, background-color 50ms;
  background-color: #d9b804;
  color: black;
}
.theme-ntos .Button--color--caution:hover {
  transition: color 0ms, background-color 0ms;
}
.theme-ntos .Button--color--caution:focus {
  transition: color 100ms, background-color 100ms;
}
.theme-ntos .Button--color--caution:hover, .theme-ntos .Button--color--caution:focus {
  background-color: #f3d00e;
  color: black;
}
.theme-ntos .Button--color--danger {
  transition: color 50ms, background-color 50ms;
  background-color: #bd2020;
  color: white;
}
.theme-ntos .Button--color--danger:hover {
  transition: color 0ms, background-color 0ms;
}
.theme-ntos .Button--color--danger:focus {
  transition: color 100ms, background-color 100ms;
}
.theme-ntos .Button--color--danger:hover, .theme-ntos .Button--color--danger:focus {
  background-color: #d52b2b;
  color: white;
}
.theme-ntos .Button--color--transparent {
  transition: color 50ms, background-color 50ms;
  background-color: #1f2b39;
  color: white;
  background-color: rgba(31, 43, 57, 0);
  color: rgba(227, 240, 255, 0.75);
}
.theme-ntos .Button--color--transparent:hover {
  transition: color 0ms, background-color 0ms;
}
.theme-ntos .Button--color--transparent:focus {
  transition: color 100ms, background-color 100ms;
}
.theme-ntos .Button--color--transparent:hover, .theme-ntos .Button--color--transparent:focus {
  background-color: #2b3847;
  color: white;
}
.theme-ntos .Button--disabled {
  background-color: #999999 !important;
}
.theme-ntos .Button--selected {
  transition: color 50ms, background-color 50ms;
  background-color: #1b9638;
  color: white;
}
.theme-ntos .Button--selected:hover {
  transition: color 0ms, background-color 0ms;
}
.theme-ntos .Button--selected:focus {
  transition: color 100ms, background-color 100ms;
}
.theme-ntos .Button--selected:hover, .theme-ntos .Button--selected:focus {
  background-color: #27ab46;
  color: white;
}
.theme-ntos .Button--altSelected--bottom::after {
  content: "";
  position: absolute;
  bottom: 0;
  right: 0;
  left: 0;
  height: 2px;
  width: 100%;
  background-color: #ffffff;
  border-radius: 2px;
}
.theme-ntos .Button--altSelected--right::after {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  height: 100%;
  width: 3px;
  background-color: #ffffff;
  border-radius: 2px;
}
.theme-ntos .Layout {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  color: #ffffff;
  background-color: #1f2b39;
  background-image: linear-gradient(to bottom, #223040 0%, #1b2633 100%);
}
.theme-ntos .Layout__titleBar {
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  width: 100%;
  height: 32px;
}
.theme-ntos .Layout__content {
  position: fixed;
  top: 32px;
  bottom: 0;
  left: 0;
  right: 0;
  overflow-x: hidden;
  overflow-y: hidden;
  margin-bottom: -6px;
  scrollbar-base-color: #17202b;
  scrollbar-face-color: #2e3f55;
  scrollbar-3dlight-color: #1f2b39;
  scrollbar-highlight-color: #1f2b39;
  scrollbar-track-color: #17202b;
  scrollbar-arrow-color: #7693b5;
  scrollbar-shadow-color: #2e3f55;
}
.theme-ntos .Layout__content--scrollable {
  overflow-y: scroll;
  margin-bottom: 0;
}
.theme-ntos .Layout__dimmer {
  position: fixed;
  top: 32px;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(55, 69, 85, 0.25);
  pointer-events: none;
}
.theme-ntos .Layout__toast {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  font-size: 12px;
  height: 40px;
  line-height: 39px;
  padding: 0 12px;
  background-color: #0f151d;
  color: rgba(255, 255, 255, 0.8);
}
.theme-ntos .Layout__resizeHandle__se {
  position: fixed;
  bottom: 0;
  right: 0;
  width: 20px;
  height: 20px;
  cursor: se-resize;
}
.theme-ntos .Layout__resizeHandle__s {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  height: 6px;
  cursor: s-resize;
}
.theme-ntos .Layout__resizeHandle__e {
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  width: 3px;
  cursor: e-resize;
}
.theme-ntos .ProgressBar {
  display: inline-block;
  position: relative;
  width: 100%;
  padding: 0 6px;
  border-radius: 2px;
  background-color: rgba(0, 0, 0, 0.5);
  transition: border-color 500ms;
}
.theme-ntos .ProgressBar__fill {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  transition: background-color 500ms, width 500ms;
}
.theme-ntos .ProgressBar__content {
  position: relative;
  line-height: 17px;
  width: 100%;
  text-align: right;
}
.theme-ntos .ProgressBar--color--default {
  border: 1px solid #384e68;
}
.theme-ntos .ProgressBar--color--default .ProgressBar__fill {
  background-color: #384e68;
}
.theme-ntos .Section {
  position: relative;
  margin-bottom: 6px;
  background-color: #1a1a1a;
  background-color: rgba(0, 0, 0, 0.33);
  box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.5);
  box-sizing: border-box;
}
.theme-ntos .Section:last-child {
  margin-bottom: 0;
}
.theme-ntos .Section__title {
  position: relative;
  padding: 6px;
  border-bottom: 2px solid #4972a1;
}
.theme-ntos .Section__titleText {
  font-size: 14px;
  font-weight: bold;
}
.theme-ntos .Section__buttons {
  position: absolute;
  display: inline-block;
  right: 6px;
  margin-top: -1px;
}
.theme-ntos .Section__content {
  padding: 8px 6px;
}
.theme-ntos .Section--level--1 .Section__titleText {
  font-size: 14px;
}
.theme-ntos .Section--level--2 .Section__titleText {
  font-size: 13px;
}
.theme-ntos .Section--level--3 .Section__titleText {
  font-size: 12px;
}
.theme-ntos .Section--level--2,
.theme-ntos .Section--level--3 {
  background-color: transparent;
  box-shadow: none;
  margin-left: -6px;
  margin-right: -6px;
}
.theme-ntos .TitleBar {
  background-color: #2a3b4e;
  border-bottom: 1px solid #161616;
  box-shadow: 0 2px 2px rgba(0, 0, 0, 0.1);
  user-select: none;
  -ms-user-select: none;
}
.theme-ntos .TitleBar__clickable {
  color: rgba(255, 255, 255, 0.5);
  background-color: #2a3b4e;
  transition: color 250ms, background-color 250ms;
}
.theme-ntos .TitleBar__clickable:hover {
  color: white;
  background-color: #cc0000;
  transition: color 0ms, background-color 0ms;
}
.theme-ntos .TitleBar__title {
  position: absolute;
  top: 0;
  left: 46px;
  color: rgba(255, 255, 255, 0.75);
  font-size: 14px;
  line-height: 31px;
  white-space: nowrap;
}
.theme-ntos .TitleBar__dragZone {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 32px;
}
.theme-ntos .TitleBar__statusIcon {
  position: absolute;
  top: 0;
  left: 12px;
  transition: color 0.5s;
  font-size: 20px;
  line-height: 32px !important;
}
.theme-ntos .TitleBar__minimize {
  position: absolute;
  top: 6px;
  right: 46px;
}
.theme-ntos .TitleBar__close {
  position: absolute;
  top: -1px;
  right: 0;
  width: 45px;
  height: 32px;
  font-size: 20px;
  line-height: 31px;
  text-align: center;
}