.theme-hackerman .Button {
  position: relative;
  display: inline-block;
  line-height: 20px;
  padding: 0 6px;
  margin-right: 2px;
  white-space: nowrap;
  outline: 0;
  border-radius: 0;
  margin-bottom: 2px;
  user-select: none;
  -ms-user-select: none;
}
.theme-hackerman .Button:last-child {
  margin-right: 0;
}
.theme-hackerman .Button .fa, .theme-hackerman .Button .fas, .theme-hackerman .Button .far {
  margin-left: -3px;
  margin-right: -3px;
  min-width: 16px;
  text-align: center;
}
.theme-hackerman .Button--hasContent .fa, .theme-hackerman .Button--hasContent .fas, .theme-hackerman .Button--hasContent .far {
  margin-right: 3px;
}
.theme-hackerman .Button--ellipsis {
  overflow: hidden;
  text-overflow: ellipsis;
}
.theme-hackerman .Button--fluid {
  display: block;
  margin-left: 0;
  margin-right: 0;
}
.theme-hackerman .Button--color--default {
  transition: color 50ms, background-color 50ms;
  background-color: #00ff00;
  color: black;
}
.theme-hackerman .Button--color--default:hover {
  transition: color 0ms, background-color 0ms;
}
.theme-hackerman .Button--color--default:focus {
  transition: color 100ms, background-color 100ms;
}
.theme-hackerman .Button--color--default:hover, .theme-hackerman .Button--color--default:focus {
  background-color: #26ff26;
  color: black;
}
.theme-hackerman .Button--color--caution {
  transition: color 50ms, background-color 50ms;
  background-color: #d9b804;
  color: black;
}
.theme-hackerman .Button--color--caution:hover {
  transition: color 0ms, background-color 0ms;
}
.theme-hackerman .Button--color--caution:focus {
  transition: color 100ms, background-color 100ms;
}
.theme-hackerman .Button--color--caution:hover, .theme-hackerman .Button--color--caution:focus {
  background-color: #f3d00e;
  color: black;
}
.theme-hackerman .Button--color--danger {
  transition: color 50ms, background-color 50ms;
  background-color: #bd2020;
  color: white;
}
.theme-hackerman .Button--color--danger:hover {
  transition: color 0ms, background-color 0ms;
}
.theme-hackerman .Button--color--danger:focus {
  transition: color 100ms, background-color 100ms;
}
.theme-hackerman .Button--color--danger:hover, .theme-hackerman .Button--color--danger:focus {
  background-color: #d52b2b;
  color: white;
}
.theme-hackerman .Button--color--transparent {
  transition: color 50ms, background-color 50ms;
  background-color: #121b12;
  color: white;
  background-color: rgba(18, 27, 18, 0);
  color: rgba(255, 255, 255, 0.5);
}
.theme-hackerman .Button--color--transparent:hover {
  transition: color 0ms, background-color 0ms;
}
.theme-hackerman .Button--color--transparent:focus {
  transition: color 100ms, background-color 100ms;
}
.theme-hackerman .Button--color--transparent:hover, .theme-hackerman .Button--color--transparent:focus {
  background-color: #1d271d;
  color: white;
}
.theme-hackerman .Button--disabled {
  background-color: #4A6A4A !important;
}
.theme-hackerman .Button--selected {
  transition: color 50ms, background-color 50ms;
  background-color: #00FF00;
  color: black;
}
.theme-hackerman .Button--selected:hover {
  transition: color 0ms, background-color 0ms;
}
.theme-hackerman .Button--selected:focus {
  transition: color 100ms, background-color 100ms;
}
.theme-hackerman .Button--selected:hover, .theme-hackerman .Button--selected:focus {
  background-color: #26ff26;
  color: black;
}
.theme-hackerman .Button--altSelected--bottom::after {
  content: "";
  position: absolute;
  bottom: 0;
  right: 0;
  left: 0;
  height: 2px;
  width: 100%;
  background-color: #ffffff;
  border-radius: 0;
}
.theme-hackerman .Button--altSelected--right::after {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  height: 100%;
  width: 3px;
  background-color: #ffffff;
  border-radius: 0;
}
.theme-hackerman .Input {
  position: relative;
  display: inline-block;
  width: 120px;
  border: 1px solid #00ff00;
  border: 1px solid rgba(0, 255, 0, 0.75);
  border-radius: 2px;
  color: #fff;
  background-color: #000;
  background-color: rgba(0, 0, 0, 0.75);
  padding: 0 4px;
  margin-right: 2px;
  line-height: 17px;
  overflow: visible;
}
.theme-hackerman .Input--fluid {
  display: block;
  width: auto;
}
.theme-hackerman .Input__baseline {
  display: inline-block;
  color: transparent;
}
.theme-hackerman .Input__input {
  display: block;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  border: 0;
  outline: 0;
  width: 100%;
  font-size: 12px;
  line-height: 17px;
  height: 17px;
  margin: 0;
  padding: 0 6px;
  font-family: Verdana, sans-serif;
  background-color: transparent;
  color: #fff;
  color: inherit;
}
.theme-hackerman .Input__input:-ms-input-placeholder {
  font-style: italic;
  color: #777;
  color: rgba(255, 255, 255, 0.45);
}
.theme-hackerman .Layout {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  color: #ffffff;
  background-color: #121b12;
  background-image: linear-gradient(to bottom, #121b12 0%, #121b12 100%);
}
.theme-hackerman .Layout__titleBar {
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  width: 100%;
  height: 32px;
}
.theme-hackerman .Layout__content {
  position: fixed;
  top: 32px;
  bottom: 0;
  left: 0;
  right: 0;
  overflow-x: hidden;
  overflow-y: hidden;
  margin-bottom: -6px;
  scrollbar-base-color: #0e140e;
  scrollbar-face-color: #253725;
  scrollbar-3dlight-color: #121b12;
  scrollbar-highlight-color: #121b12;
  scrollbar-track-color: #0e140e;
  scrollbar-arrow-color: #74a274;
  scrollbar-shadow-color: #253725;
}
.theme-hackerman .Layout__content--scrollable {
  overflow-y: scroll;
  margin-bottom: 0;
}
.theme-hackerman .Layout__dimmer {
  position: fixed;
  top: 32px;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(40, 50, 40, 0.25);
  pointer-events: none;
}
.theme-hackerman .Layout__toast {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  font-size: 12px;
  height: 40px;
  line-height: 39px;
  padding: 0 12px;
  background-color: #090e09;
  color: rgba(255, 255, 255, 0.8);
}
.theme-hackerman .Layout__resizeHandle__se {
  position: fixed;
  bottom: 0;
  right: 0;
  width: 20px;
  height: 20px;
  cursor: se-resize;
}
.theme-hackerman .Layout__resizeHandle__s {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  height: 6px;
  cursor: s-resize;
}
.theme-hackerman .Layout__resizeHandle__e {
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  width: 3px;
  cursor: e-resize;
}
.theme-hackerman .Section {
  position: relative;
  margin-bottom: 6px;
  background-color: #1a1a1a;
  background-color: rgba(0, 0, 0, 0.33);
  box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.5);
  box-sizing: border-box;
}
.theme-hackerman .Section:last-child {
  margin-bottom: 0;
}
.theme-hackerman .Section__title {
  position: relative;
  padding: 6px;
  border-bottom: 2px solid #00ff00;
}
.theme-hackerman .Section__titleText {
  font-size: 14px;
  font-weight: bold;
}
.theme-hackerman .Section__buttons {
  position: absolute;
  display: inline-block;
  right: 6px;
  margin-top: -1px;
}
.theme-hackerman .Section__content {
  padding: 8px 6px;
}
.theme-hackerman .Section--level--1 .Section__titleText {
  font-size: 14px;
}
.theme-hackerman .Section--level--2 .Section__titleText {
  font-size: 13px;
}
.theme-hackerman .Section--level--3 .Section__titleText {
  font-size: 12px;
}
.theme-hackerman .Section--level--2,
.theme-hackerman .Section--level--3 {
  background-color: transparent;
  box-shadow: none;
  margin-left: -6px;
  margin-right: -6px;
}
.theme-hackerman .TitleBar {
  background-color: #223d22;
  border-bottom: 1px solid #161616;
  box-shadow: 0 2px 2px rgba(0, 0, 0, 0.1);
  user-select: none;
  -ms-user-select: none;
}
.theme-hackerman .TitleBar__clickable {
  color: rgba(255, 255, 255, 0.5);
  background-color: #223d22;
  transition: color 250ms, background-color 250ms;
}
.theme-hackerman .TitleBar__clickable:hover {
  color: white;
  background-color: #cc0000;
  transition: color 0ms, background-color 0ms;
}
.theme-hackerman .TitleBar__title {
  position: absolute;
  top: 0;
  left: 46px;
  color: rgba(255, 255, 255, 0.75);
  font-size: 14px;
  line-height: 31px;
  white-space: nowrap;
}
.theme-hackerman .TitleBar__dragZone {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 32px;
}
.theme-hackerman .TitleBar__statusIcon {
  position: absolute;
  top: 0;
  left: 12px;
  transition: color 0.5s;
  font-size: 20px;
  line-height: 32px !important;
}
.theme-hackerman .TitleBar__minimize {
  position: absolute;
  top: 6px;
  right: 46px;
}
.theme-hackerman .TitleBar__close {
  position: absolute;
  top: -1px;
  right: 0;
  width: 45px;
  height: 32px;
  font-size: 20px;
  line-height: 31px;
  text-align: center;
}
.theme-hackerman .Layout__content {
  background-image: none;
}
.theme-hackerman .Button {
  font-family: monospace;
  border-width: 2px;
  border-style: outset;
  border-color: #00AA00;
  outline: 1px solid #007a00;
}
.theme-hackerman .candystripe:nth-child(odd) {
  background-color: rgba(0, 100, 0, 0.5);
}