.theme-syndicate .Button {
  position: relative;
  display: inline-block;
  line-height: 20px;
  padding: 0 6px;
  margin-right: 2px;
  white-space: nowrap;
  outline: 0;
  border-radius: 0;
  margin-bottom: 2px;
  user-select: none;
  -ms-user-select: none;
}
.theme-syndicate .Button:last-child {
  margin-right: 0;
}
.theme-syndicate .Button .fa, .theme-syndicate .Button .fas, .theme-syndicate .Button .far {
  margin-left: -3px;
  margin-right: -3px;
  min-width: 16px;
  text-align: center;
}
.theme-syndicate .Button--hasContent .fa, .theme-syndicate .Button--hasContent .fas, .theme-syndicate .Button--hasContent .far {
  margin-right: 3px;
}
.theme-syndicate .Button--ellipsis {
  overflow: hidden;
  text-overflow: ellipsis;
}
.theme-syndicate .Button--fluid {
  display: block;
  margin-left: 0;
  margin-right: 0;
}
.theme-syndicate .Button--color--default {
  transition: color 50ms, background-color 50ms;
  background-color: #397439;
  color: white;
}
.theme-syndicate .Button--color--default:hover {
  transition: color 0ms, background-color 0ms;
}
.theme-syndicate .Button--color--default:focus {
  transition: color 100ms, background-color 100ms;
}
.theme-syndicate .Button--color--default:hover, .theme-syndicate .Button--color--default:focus {
  background-color: #478647;
  color: white;
}
.theme-syndicate .Button--color--caution {
  transition: color 50ms, background-color 50ms;
  background-color: #be6209;
  color: white;
}
.theme-syndicate .Button--color--caution:hover {
  transition: color 0ms, background-color 0ms;
}
.theme-syndicate .Button--color--caution:focus {
  transition: color 100ms, background-color 100ms;
}
.theme-syndicate .Button--color--caution:hover, .theme-syndicate .Button--color--caution:focus {
  background-color: #d67313;
  color: white;
}
.theme-syndicate .Button--color--danger {
  transition: color 50ms, background-color 50ms;
  background-color: #9a9d00;
  color: white;
}
.theme-syndicate .Button--color--danger:hover {
  transition: color 0ms, background-color 0ms;
}
.theme-syndicate .Button--color--danger:focus {
  transition: color 100ms, background-color 100ms;
}
.theme-syndicate .Button--color--danger:hover, .theme-syndicate .Button--color--danger:focus {
  background-color: #afb30a;
  color: white;
}
.theme-syndicate .Button--color--transparent {
  transition: color 50ms, background-color 50ms;
  background-color: #550202;
  color: white;
  background-color: rgba(85, 2, 2, 0);
  color: rgba(255, 255, 255, 0.5);
}
.theme-syndicate .Button--color--transparent:hover {
  transition: color 0ms, background-color 0ms;
}
.theme-syndicate .Button--color--transparent:focus {
  transition: color 100ms, background-color 100ms;
}
.theme-syndicate .Button--color--transparent:hover, .theme-syndicate .Button--color--transparent:focus {
  background-color: #650c0c;
  color: white;
}
.theme-syndicate .Button--disabled {
  background-color: #363636 !important;
}
.theme-syndicate .Button--selected {
  transition: color 50ms, background-color 50ms;
  background-color: #9d0808;
  color: white;
}
.theme-syndicate .Button--selected:hover {
  transition: color 0ms, background-color 0ms;
}
.theme-syndicate .Button--selected:focus {
  transition: color 100ms, background-color 100ms;
}
.theme-syndicate .Button--selected:hover, .theme-syndicate .Button--selected:focus {
  background-color: #b31212;
  color: white;
}
.theme-syndicate .Button--altSelected--bottom::after {
  content: "";
  position: absolute;
  bottom: 0;
  right: 0;
  left: 0;
  height: 2px;
  width: 100%;
  background-color: #ffffff;
  border-radius: 0;
}
.theme-syndicate .Button--altSelected--right::after {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  height: 100%;
  width: 3px;
  background-color: #ffffff;
  border-radius: 0;
}
.theme-syndicate .Input {
  position: relative;
  display: inline-block;
  width: 120px;
  border: 1px solid #87ce87;
  border: 1px solid rgba(135, 206, 135, 0.75);
  border-radius: 2px;
  color: #fff;
  background-color: #000;
  background-color: rgba(0, 0, 0, 0.75);
  padding: 0 4px;
  margin-right: 2px;
  line-height: 17px;
  overflow: visible;
}
.theme-syndicate .Input--fluid {
  display: block;
  width: auto;
}
.theme-syndicate .Input__baseline {
  display: inline-block;
  color: transparent;
}
.theme-syndicate .Input__input {
  display: block;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  border: 0;
  outline: 0;
  width: 100%;
  font-size: 12px;
  line-height: 17px;
  height: 17px;
  margin: 0;
  padding: 0 6px;
  font-family: Verdana, sans-serif;
  background-color: transparent;
  color: #fff;
  color: inherit;
}
.theme-syndicate .Input__input:-ms-input-placeholder {
  font-style: italic;
  color: #777;
  color: rgba(255, 255, 255, 0.45);
}
.theme-syndicate .Layout {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  color: #ffffff;
  background-color: #550202;
  background-image: linear-gradient(to bottom, #730303 0%, #370101 100%);
}
.theme-syndicate .Layout__titleBar {
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  width: 100%;
  height: 32px;
}
.theme-syndicate .Layout__content {
  position: fixed;
  top: 32px;
  bottom: 0;
  left: 0;
  right: 0;
  overflow-x: hidden;
  overflow-y: hidden;
  margin-bottom: -6px;
  scrollbar-base-color: #400202;
  scrollbar-face-color: #7e0303;
  scrollbar-3dlight-color: #550202;
  scrollbar-highlight-color: #550202;
  scrollbar-track-color: #400202;
  scrollbar-arrow-color: #fa3030;
  scrollbar-shadow-color: #7e0303;
}
.theme-syndicate .Layout__content--scrollable {
  overflow-y: scroll;
  margin-bottom: 0;
}
.theme-syndicate .Layout__dimmer {
  position: fixed;
  top: 32px;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(117, 22, 22, 0.25);
  pointer-events: none;
}
.theme-syndicate .Layout__toast {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  font-size: 12px;
  height: 40px;
  line-height: 39px;
  padding: 0 12px;
  background-color: #2b0101;
  color: rgba(255, 255, 255, 0.8);
}
.theme-syndicate .Layout__resizeHandle__se {
  position: fixed;
  bottom: 0;
  right: 0;
  width: 20px;
  height: 20px;
  cursor: se-resize;
}
.theme-syndicate .Layout__resizeHandle__s {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  height: 6px;
  cursor: s-resize;
}
.theme-syndicate .Layout__resizeHandle__e {
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  width: 3px;
  cursor: e-resize;
}
.theme-syndicate .NoticeBox {
  padding: 4px 6px;
  margin-bottom: 6px;
  box-shadow: none;
  font-weight: bold;
  font-style: italic;
  color: white;
  background-color: #750000;
  background-image: repeating-linear-gradient(-45deg, #750000, #750000 10px, #910101 10px, #910101 20px);
}
