.theme-syndicate .ProgressBar {
  display: inline-block;
  position: relative;
  width: 100%;
  padding: 0 6px;
  border-radius: 0;
  background-color: rgba(0, 0, 0, 0.5);
  transition: border-color 500ms;
}
.theme-syndicate .ProgressBar__fill {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  transition: background-color 500ms, width 500ms;
}
.theme-syndicate .ProgressBar__content {
  position: relative;
  line-height: 17px;
  width: 100%;
  text-align: right;
}
.theme-syndicate .ProgressBar--color--default {
  border: 1px solid #306330;
}
.theme-syndicate .ProgressBar--color--default .ProgressBar__fill {
  background-color: #306330;
}
.theme-syndicate .Section {
  position: relative;
  margin-bottom: 6px;
  background-color: #1a1a1a;
  background-color: rgba(0, 0, 0, 0.33);
  box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.5);
  box-sizing: border-box;
}
.theme-syndicate .Section:last-child {
  margin-bottom: 0;
}
.theme-syndicate .Section__title {
  position: relative;
  padding: 6px;
  border-bottom: 2px solid #397439;
}
.theme-syndicate .Section__titleText {
  font-size: 14px;
  font-weight: bold;
}
.theme-syndicate .Section__buttons {
  position: absolute;
  display: inline-block;
  right: 6px;
  margin-top: -1px;
}
.theme-syndicate .Section__content {
  padding: 8px 6px;
}
.theme-syndicate .Section--level--1 .Section__titleText {
  font-size: 14px;
}
.theme-syndicate .Section--level--2 .Section__titleText {
  font-size: 13px;
}
.theme-syndicate .Section--level--3 .Section__titleText {
  font-size: 12px;
}
.theme-syndicate .Section--level--2,
.theme-syndicate .Section--level--3 {
  background-color: transparent;
  box-shadow: none;
  margin-left: -6px;
  margin-right: -6px;
}
.theme-syndicate .TitleBar {
  background-color: #910101;
  border-bottom: 1px solid #161616;
  box-shadow: 0 2px 2px rgba(0, 0, 0, 0.1);
  user-select: none;
  -ms-user-select: none;
}
.theme-syndicate .TitleBar__clickable {
  color: rgba(255, 255, 255, 0.5);
  background-color: #910101;
  transition: color 250ms, background-color 250ms;
}
.theme-syndicate .TitleBar__clickable:hover {
  color: white;
  background-color: #cc0000;
  transition: color 0ms, background-color 0ms;
}
.theme-syndicate .TitleBar__title {
  position: absolute;
  top: 0;
  left: 46px;
  color: rgba(255, 255, 255, 0.75);
  font-size: 14px;
  line-height: 31px;
  white-space: nowrap;
}
.theme-syndicate .TitleBar__dragZone {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 32px;
}
.theme-syndicate .TitleBar__statusIcon {
  position: absolute;
  top: 0;
  left: 12px;
  transition: color 0.5s;
  font-size: 20px;
  line-height: 32px !important;
}
.theme-syndicate .TitleBar__minimize {
  position: absolute;
  top: 6px;
  right: 46px;
}
.theme-syndicate .TitleBar__close {
  position: absolute;
  top: -1px;
  right: 0;
  width: 45px;
  height: 32px;
  font-size: 20px;
  line-height: 31px;
  text-align: center;
}
.theme-syndicate .Tooltip {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  font-style: normal;
  font-weight: normal;
}
.theme-syndicate .Tooltip::after {
  position: absolute;
  display: block;
  white-space: nowrap;
  z-index: 2;
  padding: 6px 10px;
  transform: translateX(-50%);
  pointer-events: none;
  visibility: hidden;
  opacity: 0;
  text-align: left;
  content: attr(data-tooltip);
  transition: all 150ms;
  background-color: #4a0202;
  box-shadow: 1px 1px 15px -1px rgba(0, 0, 0, 0.5);
  border-radius: 2px;
}
.theme-syndicate .Tooltip:hover::after {
  transition: all 70ms;
  pointer-events: none;
  visibility: visible;
  opacity: 1;
}
.theme-syndicate .Tooltip--long::after {
  width: 250px;
  white-space: normal;
}
.theme-syndicate .Tooltip--top::after {
  bottom: 100%;
  left: 50%;
  transform: translateX(-50%) translateY(8px);
}
.theme-syndicate .Tooltip--top:hover::after {
  transform: translateX(-50%) translateY(-8px);
}
.theme-syndicate .Tooltip--bottom::after {
  top: 100%;
  left: 50%;
  transform: translateX(-50%) translateY(-8px);
}
.theme-syndicate .Tooltip--bottom:hover::after {
  transform: translateX(-50%) translateY(8px);
}
.theme-syndicate .Tooltip--bottom-left::after {
  top: 100%;
  right: 50%;
  transform: translateX(12px) translateY(-8px);
}
.theme-syndicate .Tooltip--bottom-left:hover::after {
  transform: translateX(12px) translateY(8px);
}
.theme-syndicate .Tooltip--bottom-right::after {
  top: 100%;
  left: 50%;
  transform: translateX(-12px) translateY(-8px);
}
.theme-syndicate .Tooltip--bottom-right:hover::after {
  transform: translateX(-12px) translateY(8px);
}
.theme-syndicate .Tooltip--left::after {
  top: 50%;
  right: 100%;
  transform: translateX(8px) translateY(-50%);
}
.theme-syndicate .Tooltip--left:hover::after {
  transform: translateX(-8px) translateY(-50%);
}
.theme-syndicate .Tooltip--right::after {
  top: 50%;
  left: 100%;
  transform: translateX(-8px) translateY(-50%);
}
.theme-syndicate .Tooltip--right:hover::after {
  transform: translateX(8px) translateY(-50%);
}
