.theme-retro .Button {
  position: relative;
  display: inline-block;
  line-height: 20px;
  padding: 0 6px;
  margin-right: 2px;
  white-space: nowrap;
  outline: 0;
  border-radius: 0;
  margin-bottom: 2px;
  user-select: none;
  -ms-user-select: none;
}
.theme-retro .Button:last-child {
  margin-right: 0;
}
.theme-retro .Button .fa, .theme-retro .Button .fas, .theme-retro .Button .far {
  margin-left: -3px;
  margin-right: -3px;
  min-width: 16px;
  text-align: center;
}
.theme-retro .Button--hasContent .fa, .theme-retro .Button--hasContent .fas, .theme-retro .Button--hasContent .far {
  margin-right: 3px;
}
.theme-retro .Button--ellipsis {
  overflow: hidden;
  text-overflow: ellipsis;
}
.theme-retro .Button--fluid {
  display: block;
  margin-left: 0;
  margin-right: 0;
}
.theme-retro .Button--color--default {
  transition: color 50ms, background-color 50ms;
  background-color: #E8E4C9;
  color: black;
}
.theme-retro .Button--color--default:hover {
  transition: color 0ms, background-color 0ms;
}
.theme-retro .Button--color--default:focus {
  transition: color 100ms, background-color 100ms;
}
.theme-retro .Button--color--default:hover, .theme-retro .Button--color--default:focus {
  background-color: #f7f6ee;
  color: black;
}
.theme-retro .Button--color--caution {
  transition: color 50ms, background-color 50ms;
  background-color: #be6209;
  color: white;
}
.theme-retro .Button--color--caution:hover {
  transition: color 0ms, background-color 0ms;
}
.theme-retro .Button--color--caution:focus {
  transition: color 100ms, background-color 100ms;
}
.theme-retro .Button--color--caution:hover, .theme-retro .Button--color--caution:focus {
  background-color: #d67313;
  color: white;
}
.theme-retro .Button--color--danger {
  transition: color 50ms, background-color 50ms;
  background-color: #9a9d00;
  color: white;
}
.theme-retro .Button--color--danger:hover {
  transition: color 0ms, background-color 0ms;
}
.theme-retro .Button--color--danger:focus {
  transition: color 100ms, background-color 100ms;
}
.theme-retro .Button--color--danger:hover, .theme-retro .Button--color--danger:focus {
  background-color: #afb30a;
  color: white;
}
.theme-retro .Button--color--transparent {
  transition: color 50ms, background-color 50ms;
  background-color: #E8E4C9;
  color: black;
  background-color: rgba(232, 228, 201, 0);
  color: rgba(255, 255, 255, 0.5);
}
.theme-retro .Button--color--transparent:hover {
  transition: color 0ms, background-color 0ms;
}
.theme-retro .Button--color--transparent:focus {
  transition: color 100ms, background-color 100ms;
}
.theme-retro .Button--color--transparent:hover, .theme-retro .Button--color--transparent:focus {
  background-color: #f7f6ee;
  color: black;
}
.theme-retro .Button--disabled {
  background-color: #363636 !important;
}
.theme-retro .Button--selected {
  transition: color 50ms, background-color 50ms;
  background-color: #9d0808;
  color: white;
}
.theme-retro .Button--selected:hover {
  transition: color 0ms, background-color 0ms;
}
.theme-retro .Button--selected:focus {
  transition: color 100ms, background-color 100ms;
}
.theme-retro .Button--selected:hover, .theme-retro .Button--selected:focus {
  background-color: #b31212;
  color: white;
}
.theme-retro .Button--altSelected--bottom::after {
  content: "";
  position: absolute;
  bottom: 0;
  right: 0;
  left: 0;
  height: 2px;
  width: 100%;
  background-color: #ffffff;
  border-radius: 0;
}
.theme-retro .Button--altSelected--right::after {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  height: 100%;
  width: 3px;
  background-color: #ffffff;
  border-radius: 0;
}
.theme-retro .Layout {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  color: #ffffff;
  background-color: #E8E4C9;
  background-image: linear-gradient(to bottom, #e8e4c9 0%, #e8e4c9 100%);
}
.theme-retro .Layout__titleBar {
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  width: 100%;
  height: 32px;
}
.theme-retro .Layout__content {
  position: fixed;
  top: 32px;
  bottom: 0;
  left: 0;
  right: 0;
  overflow-x: hidden;
  overflow-y: hidden;
  margin-bottom: -6px;
  scrollbar-base-color: #c8be7d;
  scrollbar-face-color: #eae7ce;
  scrollbar-3dlight-color: #e8e4c9;
  scrollbar-highlight-color: #e8e4c9;
  scrollbar-track-color: #c8be7d;
  scrollbar-arrow-color: #f4f2e4;
  scrollbar-shadow-color: #eae7ce;
}
.theme-retro .Layout__content--scrollable {
  overflow-y: scroll;
  margin-bottom: 0;
}
.theme-retro .Layout__dimmer {
  position: fixed;
  top: 32px;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(251, 250, 246, 0.25);
  pointer-events: none;
}
.theme-retro .Layout__toast {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  font-size: 12px;
  height: 40px;
  line-height: 39px;
  padding: 0 12px;
  background-color: #988d41;
  color: rgba(255, 255, 255, 0.8);
}
.theme-retro .Layout__resizeHandle__se {
  position: fixed;
  bottom: 0;
  right: 0;
  width: 20px;
  height: 20px;
  cursor: se-resize;
}
.theme-retro .Layout__resizeHandle__s {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  height: 6px;
  cursor: s-resize;
}
.theme-retro .Layout__resizeHandle__e {
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  width: 3px;
  cursor: e-resize;
}
.theme-retro .ProgressBar {
  display: inline-block;
  position: relative;
  width: 100%;
  padding: 0 6px;
  border-radius: 0;
  background-color: rgba(0, 0, 0, 0.5);
  transition: border-color 500ms;
}
.theme-retro .ProgressBar__fill {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  transition: background-color 500ms, width 500ms;
}
.theme-retro .ProgressBar__content {
  position: relative;
  line-height: 17px;
  width: 100%;
  text-align: right;
}
.theme-retro .ProgressBar--color--default {
  border: 1px solid black;
}
.theme-retro .ProgressBar--color--default .ProgressBar__fill {
  background-color: black;
}
.theme-retro .Section {
  position: relative;
  margin-bottom: 6px;
  background-color: #1a1a1a;
  background-color: rgba(0, 0, 0, 0.33);
  box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.5);
  box-sizing: border-box;
}
.theme-retro .Section:last-child {
  margin-bottom: 0;
}
.theme-retro .Section__title {
  position: relative;
  padding: 6px;
  border-bottom: 2px solid #000000;
}
.theme-retro .Section__titleText {
  font-size: 14px;
  font-weight: bold;
}
.theme-retro .Section__buttons {
  position: absolute;
  display: inline-block;
  right: 6px;
  margin-top: -1px;
}
.theme-retro .Section__content {
  padding: 8px 6px;
}
.theme-retro .Section--level--1 .Section__titleText {
  font-size: 14px;
}
.theme-retro .Section--level--2 .Section__titleText {
  font-size: 13px;
}
.theme-retro .Section--level--3 .Section__titleText {
  font-size: 12px;
}
.theme-retro .Section--level--2,
.theme-retro .Section--level--3 {
  background-color: transparent;
  box-shadow: none;
  margin-left: -6px;
  margin-right: -6px;
}
.theme-retro .TitleBar {
  background-color: #585337;
  border-bottom: 1px solid #161616;
  box-shadow: 0 2px 2px rgba(0, 0, 0, 0.1);
  user-select: none;
  -ms-user-select: none;
}
.theme-retro .TitleBar__clickable {
  color: rgba(255, 255, 255, 0.5);
  background-color: #585337;
  transition: color 250ms, background-color 250ms;
}
.theme-retro .TitleBar__clickable:hover {
  color: white;
  background-color: #cc0000;
  transition: color 0ms, background-color 0ms;
}
.theme-retro .TitleBar__title {
  position: absolute;
  top: 0;
  left: 46px;
  color: rgba(255, 255, 255, 0.75);
  font-size: 14px;
  line-height: 31px;
  white-space: nowrap;
}
.theme-retro .TitleBar__dragZone {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 32px;
}
.theme-retro .TitleBar__statusIcon {
  position: absolute;
  top: 0;
  left: 12px;
  transition: color 0.5s;
  font-size: 20px;
  line-height: 32px !important;
}
.theme-retro .TitleBar__minimize {
  position: absolute;
  top: 6px;
  right: 46px;
}
.theme-retro .TitleBar__close {
  position: absolute;
  top: -1px;
  right: 0;
  width: 45px;
  height: 32px;
  font-size: 20px;
  line-height: 31px;
  text-align: center;
}
.theme-retro .Button {
  font-family: monospace;
  color: #161613;
  border-width: 8px;
  border-style: outset;
  border-color: #E8E4C9;
  outline: 3px solid #161613;
}
.theme-retro .Layout__content {
  background-image: none;
}